//Colors
$dark-gray: #222;
$gray: #444;
$light-gray: #999;
$link-hover: #78a2a0;
$off-white: #ebe9e9;

body,
html {
  box-sizing: border-box;
  // background: $dark-gray;
  color: $off-white;
  font: 400 15px/1.8 'Open Sans', sans-serif;
  height: 100%;
  margin: 0;
}

body {
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

#showcase {
  min-height: 600px;
  color: $off-white;
  text-align: center;
}

#showcase .bgimg-1 {
  position: absolute;
  width: 100%;
  opacity: 0.7;
}

#footer {
  height: 450px;
}

.bgimg-1,
.bgimg-2,
.bgimg-3 {
  position: relative;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  opacity: 1;
}

.bgimg-1 {
  background-image: url('./images/puako-bw.jpg');
  min-height: 100%;
  z-index: -1;
}

.bgimg-2 {
  // background-image: url("./images/puako-bw.jpg");
  // min-height: 300px;
}

.bgimg-3 {
  // background-image: url("./images/puako-bw.jpg");
  // min-height: 200px;
}

.color-block {
  font-size: 1.8rem;
  font-weight: 700;
  letter-spacing: 8px;
  margin: 0 auto;
  padding: 1rem;
  width: 40%;
}

.color-block span {
  display: block;
  font-size: 0.9rem;
  font-weight: 300;
  letter-spacing: 8px;
}

.caption {
  position: absolute;
  left: 0;
  opacity: 1;
  top: 15%;
  width: 100%;
  text-align: center;
  color: $off-white;
}

.caption span.border {
  padding: 18px;
  letter-spacing: 8px;
}

h3 {
  letter-spacing: 5px;
  text-transform: uppercase;
  font: 25px 'Lato', sans-serif;
  text-align: center;
}

.content {
  text-align: center;
  padding: 50px 80px;
  text-align: justify;
}

#client-block.content {
  // margin:2rem;
  // padding:50px 80px;
}

.light-color-font {
  background-color: $light-gray;
}

.dark-color-font {
  color: #222;
}

.gray-color-font {
  color: #777;
}

.white {
  color: white;
}

.white-bg {
  background-image: #fff;
}

.light-color-bg {
  background: lighten($light-gray, 20%);
}

.medium-color-bg {
  background: #4e797f;
}

.dark-color-bg {
  background: $gray;
}

.section-headers {
  background-color: transparent;
  font-size: 2rem;
  color: #fff;
}

span.contact-text {
  letter-spacing: normal;
}

/*WORK*/
#work-holder {
  width: 100%;
  text-align: center;
  background: $light-gray;
}
#work-holder h3 {
  margin: 0;
}
