    margin:2rem 0 0 0;
  } */
/* SLIDER */
.slider {
  background-color: $dark-gray;
  /* border-radius: 8px 8px 0 0; */
  height: 0;
  color: #fff;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  text-align: center;
  top: 100%;
  /* left:5%; */
  margin: 0 auto;
  transition: all 0.3s;
  width: 100%;
}

.slider:hover {
  cursor: pointer;
}

/* .slider.open {
    height: 100%;
    top: 0;
  } */
.slider.close {
  height: 100%;
  opacity: 0.85;
  top: 0;
}

.slider h3 {
  letter-spacing: 3px;
  padding: 2rem 0 1rem 0;
}

.slider .hover-text {
  font-size: 2rem;
  line-height: 2;
  padding: 0.5rem 1.5rem;
  text-align: justify;
}

.slider a {
  color: $link-hover;
  text-decoration: none;
}
.contact-text a {
  color: $off-white;
  cursor: pointer;
  font-weight: 400;
  text-decoration: none;
  z-index: 10;
}
.contact-text a:hover {
  color: $link-hover;
}

.py-3 {
  padding: 3rem;
}

/* Turn off parallax scrolling for tablets and phones */
@media only screen and (max-device-width: 1024px) {
  .bgimg-1,
  .bgimg-2,
  .bgimg-3 {
    background-attachment: scroll;
  }
}

@media (min-width: 768px) {
  p {
    text-align: center;
  }
  /* WORK */
  .work-holder {
    width: 100%;
    display: flex;
    /* background-image: url("../imgs/urban-city-view-street-black-600w-1023602029.webp"); */
    // background: rgba(0, 0, 0, 0);
  }

  .work-container {
    display: grid;
    width: 100%;
    grid-template-columns: 45% 45%;
    justify-content: center;
    align-items: center;
  }

  .item {
    position: relative;
    align-self: center;
    justify-self: center;
    text-align: center;
    height: auto;
    margin: 1rem;
    max-width: 100%;
    /* margin:1rem; */
  }
  .item a {
    display: block;
  }

  .item img {
    max-width: 100%;
    display: block;
    text-align: center;
    margin: 0 auto;
    /* height: auto; */
    /* padding:1rem; */
    /* margin:0 auto; */
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
  }

  .slider .hover-text {
    font-size: 1rem;
    line-height: 1.5;
    padding: 0.5rem 1.5rem;
    text-align: justify;
  }

  .slider h3 {
    font-size: 1.4rem;
  }

  .slider p {
    margin: 0;
  }

  .slider .hover-text {
    font-size: 1rem;
    line-height: 1.5;
    padding: 0.5rem 1.5rem;
    text-align: justify;
  }
}
@media (min-width: 1024px) {
  .slider h3 {
    font-size: 1.8rem;
  }

  .slider .hover-text {
    font-size: 1.1rem;
    line-height: 1.5;
    padding: 0.5rem 1.5rem;
    text-align: justify;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .item a {
    display: block;
  }
  .slider .hover-text {
    font-size: 0.9rem;
    line-height: 1.5;
    padding: 0.5rem 1.5rem;
    text-align: justify;
  }
}
@media (max-width: 768px) {
  .color-block {
    font-size: 1.8rem;
    font-weight: 700;
    letter-spacing: 6px;
    margin: 0 auto;
    padding: 1rem;
    width: 70%;
  }

  .color-block span {
    display: block;
    font-size: 0.9rem;
    font-weight: 300;
    /* letter-spacing: 8px; */
    text-align: center;
  }

  .content {
    text-align: center;
    padding: 45px 40px;
    text-align: justify;
  }

  /* WORK */
  #work-holder {
    display: block;
    /* background-image: url("../imgs/urban-city-view-street-black-600w-1023602029.webp"); */
    // background: rgba(0, 0, 0, 0);
  }

  .work-container {
    display: inline-block;
    width: 100%;
  }

  .item {
    position: relative;
    align-self: center;
    justify-self: center;
    text-align: center;
    height: auto;
    margin: 1rem 0;
    max-width: 100%;
    /* margin:1rem; */
  }

  .item a {
    display: none;
  }

  .item img {
    cursor: pointer;
    max-width: 100%;
    display: block;
    text-align: center;
    margin: 0 auto;
    /* height: auto; */
    /* padding:1rem; */
    /* margin:0 auto; */
    /* -webkit-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.3);
      -moz-box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.3);
      box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.3); */
  }

  /* SLIDER */
  .slider {
    background-color: $dark-gray;
    /* border-radius: 8px 8px 0 0; */
    height: 75%;
    color: #fff;
    opacity: 0.9;
    overflow: hidden;
    position: absolute;
    text-align: center;
    top: 25%;
    /* left:5%; */
    margin: 0 auto;
    transition: all 0.2s;
    width: 100%;
  }

  .slider:hover {
    cursor: pointer;
  }

  /* .slider.open {
      height: 100%;
      top: 0;
    } */
  .slider.close {
    height: 75%;
    top: 25%;
  }

  .slider h3 {
    letter-spacing: 3px;
    line-height: 1.4;
    font-size: 1.3rem;
    padding: 0.9rem 0.5rem 0.75rem 0.5rem;
    margin: 1rem 0 0 0;
  }

  .slider .hover-text {
    font-size: 0.8rem;
    line-height: 1.4;
    margin: 0.15rem 0;
    padding: 0.5rem 1rem;
    text-align: justify;
  }
}

